import React from "react";
import { Button, Menu, Grid, GridColumn, Image,Header, Container } from 'semantic-ui-react';
import 'semantic-ui-css/semantic.min.css';
import logo from "../Resources/IMG_2120.jpg";
import parse from 'html-react-parser'

let items = {
    description: 'Student Uniwersytetu Gdaskiego, z dowiadczeniem w realizacji <a href="#" id="highlight"> komercyjnych projektw.</a> Obecnie kontynuuj swj rozwj w zakresietworzenia <a href="#" id="highlight">kodu wysokiej jakoci</a>. Dziki mojemu zaangaowaniu i pasji, udao mi si wdroy w ycie kilka interesujcych projektw, dajcych mi moliwo zdobycia cennego dowiadczenia i uzupenienia moich umiejtnoci. ',
    buttonText: "Sprawd moje projekty"
}


export default function PorftolioComponent(language) {

    if(language.language == "en"){
        items = {
            description: 'Student at the University of Gdask with experience in implementing <a href="#" id="highlight">commercial projects</a>. Currently, I am continuing my development in the area of creating <a href="#" id="highlight">high-quality code</a>. Thanks to my commitment and passion, I have successfully brought several interesting projects to life, giving me the opportunity to gain valuable experience and improve my skills.',
            buttonText: "Check out my projects"
        }
    }

    return(
        <Grid stackable verticalAlign='middle' columns={2}>
            <GridColumn textAlign="left" id="firstParagraph">
                <Container>
                <Header as='h1' id="tytul">Marcin Pestka</Header>
                <Header as='h3' id="pod_tytul">Full-stack developer</Header>
                <p id="opisMnie">{parse(items.description)}</p>
                <Container textAlign="center">
                <Button className="button-portfolio" href="#">{items.buttonText}</Button>
                </Container>
                </Container>
            </GridColumn>
            <GridColumn centered id="photo_column">
            <Image src={logo} circular />
            </GridColumn>
        </Grid>
    )
}